.animation {
    animation: changeBackgroundImg 30s infinite;
    height: 800px;
    background-image: url('./frontImg.png');
    background-repeat: no-repeat;
}

@keyframes changeBackgroundImg {
    25% {
      background-image: url('./familia.png');
    }
    50% {
      background-image: url('./penal.png');
    }
    75% {
      background-image: url('./comercio.png');
    }
    100% {
      background-image: url('./consumidor.png');
    }
  }